.container-fluid {
    max-width: 1200px;
  }
  
  .nav-tabs .nav-link {
    cursor: pointer;
  }
  
  @media (max-width: 575px) {
    .navbar-toggleable > .container {
        width: 100%;
        padding-right: 15px;
        padding-left: 15px;
    }
  }
  
  .jumbotron-header, .jumbotron-header p:last-child, .navbar {
    margin-bottom: 0
  }
  
  .jumbotron-header .btn {
    margin-right: 1rem;
  }
  
  .jumbotron-header .btn + .btn {
    margin-right: 0;
  }
  
  @media (max-width: 543px) {
    .jumbotron-header .btn {
      display: block;
      margin: 0 0 1rem 0;
    }
  }
  
  .content {
    margin-top: 2rem;
  }
  
  @media(min-width: 768px) {
    html,body,#app,#app > .wrapper,#app >.wrapper > .container,  #app >.wrapper > .container > .row{
      height:100%;
    }
    .docSearch-navigation, .docSearch-content {
      overflow-y: scroll;
      height:100%;
    }
  }
  
  
  .header .navbar-nav .nav-link.active {
    color: #333;
  }
  
  .header.navbar {
    padding-left: 0;
    padding-right: 0;
  }
  
  .footer {
    margin-top: 2rem;
    padding: 2rem 0;
    border-top: 1px solid #e7e7e7;
  }
  
  .footer .social {
    margin-bottom: 0;
  }
  
  .footer .social a {
    display: inline-block;
  }
  
  .footer .social img {
    display: block;
  }
  
  .navbar-collapse .container-fluid {
    padding: 2rem 2.5rem;
    border-bottom: 1px solid #55595c
  }
  .navbar-collapse .text-muted, .navbar-collapse h4 {
    color: #818a91
  }
  .about {
    float: left;
    max-width: 30rem;
    margin-right: 3rem
  }
  .social a {
    font-weight: 500;
    color: #eceeef
  }
  .social a:hover {
    color: #fff
  }
  .jumbotron-header {
    padding-top: 2rem;
    padding-bottom: 2rem;
    background-color: #fff
  }
  .jumbotron-heading {
    font-weight: 300
  }
  
  footer {
    padding-top: 3rem;
    padding-bottom: 3rem
  }
  footer p {
    margin-bottom: .25rem
  }
  
  h1,h2,h3,h4,h5,h6,strong,b {
    font-weight: 300;
  }
  
  h1,h2,h3 {
    margin-bottom: 3rem;
  }
  
  .border-box {
    position: relative;
    padding: 2em 1em 1em;
    border: 1px solid #d8d8d8;
    margin: 1.5rem 0 0;
  }
  
  .border-box .card {
    max-width: 320px;
  }
  
  pre, code {
    background: #f5f5f5;
    border-radius: 0;
    border-color: #d8d8d8;
    margin: 0;
  }
  
  .border-box + pre {
    border: 1px solid #d8d8d8;
    border-top: none;
    padding: 1em;
    margin-top: 0 !important;
    clear: both;
  }
  
  .docs-sidebar .nav-item {
    padding: .2rem 0 .2rem 0;
  }
  
  .docs-sidebar .nav-link {
    color: #888;
    padding: .3rem .5rem .3rem 1rem;
    border-left: 2px solid transparent;
  }
  
  .docs-sidebar .nav-link:hover {
    color: #777
  }
  
  .docs-sidebar .nav-link.active {
    color: #666;
    border-left: 2px solid #d9534f;
  }
  
  .border-box > .container .row {
    margin-bottom: 1rem;
  }
  
  .border-box > .container .row > [class^="col"] {
    padding-top: .75rem;
    padding-bottom: .75rem;
    background-color: #E5EDF5;
    border: 1px solid #C9C1D5;
    color: #5F5F5F;
  }
  
  .border-box .form-control + .form-control {
      margin-top: .5rem;
  }
  
  .border-box .nav-tabs {
    margin-bottom: 1rem;
  }
  
  .border-box .progress {
    margin-bottom: 1rem;
  }
  
  
  /* http://prismjs.com/download.html?themes=prism-okaidia&languages=markup+css+clike+javascript+bash+jsx&plugins=autolinker */
  /**
   * okaidia theme for JavaScript, CSS and HTML
   * Loosely based on Monokai textmate theme by http://www.monokai.nl/
   * @author ocodia
   */
  
   code[class*="language-"],
   pre[class*="language-"] {
     color: #f8f8f2;
     background: none;
     text-shadow: 0 1px rgba(0, 0, 0, 0.3);
     text-align: left;
     white-space: pre;
     word-spacing: normal;
     word-break: normal;
     word-wrap: normal;
     line-height: 1.5;
  
     -moz-tab-size: 4;
     tab-size: 4;
  
     -webkit-hyphens: none;
     hyphens: none;
   }
  
   /* Code blocks */
   pre[class*="language-"] {
     padding: 1em;
     margin: 1.5em 0;
     overflow: auto;
   }
  
   :not(pre) > code[class*="language-"],
   pre[class*="language-"] {
     background: #272822;
   }
  
   /* Inline code */
   :not(pre) > code[class*="language-"] {
     padding: .1em;
     border-radius: .3em;
     white-space: normal;
   }
  
   .token.comment,
   .token.prolog,
   .token.doctype,
   .token.cdata {
     color: slategray;
   }
  
   .token.punctuation {
     color: #f8f8f2;
   }
  
   .namespace {
     opacity: .7;
   }
  
   .token.property,
   .token.tag,
   .token.constant,
   .token.symbol,
   .token.deleted {
     color: #f92672;
   }
  
   .token.boolean,
   .token.number {
     color: #ae81ff;
   }
  
   .token.selector,
   .token.attr-name,
   .token.string,
   .token.char,
   .token.builtin,
   .token.inserted {
     color: #a6e22e;
   }
  
   .token.operator,
   .token.entity,
   .token.url,
   .language-css .token.string,
   .style .token.string,
   .token.variable {
     color: #f8f8f2;
   }
  
   .token.atrule,
   .token.attr-value,
   .token.function {
     color: #e6db74;
   }
  
   .token.keyword {
     color: #66d9ef;
   }
  
   .token.regex,
   .token.important {
     color: #fd971f;
   }
  
   .token.important,
   .token.bold {
     font-weight: bold;
   }
   .token.italic {
     font-style: italic;
   }
  
   .token.entity {
     cursor: help;
   }
  
   .token a {
     color: inherit;
   }
  
  code .tag {
    font-size: inherit;
    padding: 0;
    display: inherit;
  }
  
  .bg-docs-transparent-grid {
    background: url(../../static/media/transparent.b1245fda.svg);
  }
